:root {
  --main-font: "Lucida Console", "Courier New", monospace;
  --bg-color: #000000;
  --accent-color: #ffeb3b;
  --text-color: #ffffff;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;


}


html {
  scroll-behavior: smooth;
  overflow-x: hidden;
}

body {
  font-family: var(--main-font);
  background: var(--bg-color);
  color: var(--text-color);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
}


.app {
  text-align: center;
  width: 100%;
  overflow-x: hidden;
}

header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 5vh;
  background: var(--bg-color);
  color: var(--text-color);
  z-index: 1000;
  display: flex;
  align-items: center;
}

.header-logo {
  color: var(--accent-color);
  font-weight: bold;
  font-size: 1.3rem;
  text-decoration: none;
  margin-left: 2rem;
  margin-right: auto;
  letter-spacing: 1px;
  transition: opacity 0.2s;
}

.header-logo:hover {
  opacity: 0.7;
}

nav {
  display: flex;
  gap: 2rem;
  padding: 0 2rem;
}

.nav-link {
  color: var(--text-color);
  text-decoration: none;
  font-weight: 600;
  transition: transform 0.3s;
}

.nav-link:hover {
  transform: translateY(-2px);
}

main {
  padding-top: 5vh;
}

.page-container {
  width: 100%;
}

.intro-section {
  width: 100%;
  min-height: 95vh;
  background-color: var(--accent-color);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 6vh 0 15vh 0;
}

.logo-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
}

.logo-container img {
  height: 40vh;
  width: auto;
}

.contact-button {
  background-color: var(--accent-color);
  color: var(--bg-color);
  font-family: var(--main-font);
  font-size: 2rem;
  font-weight: 600;
  border: 3px solid var(--bg-color);
  border-radius: 100px;
  padding: 1.2rem 2.5rem;
  text-decoration: none;
  transition: transform 0.2s;
  display: inline-block;
}

.contact-button:hover {
  transform: translateY(-2px);
}

.about-section {
  background: var(--bg-color);
}

.info-section {
  width: 100%;
  min-height: 75vh;
  background: var(--bg-color);
  color: var(--text-color);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 15vh 2rem 2rem 2rem;
  text-align: center;
  opacity: 0;
  transform: translateY(50px);
  transition: opacity 0.8s cubic-bezier(0.25, 0.46, 0.45, 0.94), 
              transform 0.8s cubic-bezier(0.25, 0.46, 0.45, 0.94);
}

.info-section.animate-in {
  opacity: 1;
  transform: translateY(0);
}

.info-section#contact {
  opacity: 1;
  transform: translateY(0);
  transition: none;
}

.info-question {
  color: var(--accent-color);
  font-size: 2.5rem;
  font-weight: bold;
  margin-bottom: 2rem;
  letter-spacing: 0.5px;
  opacity: 0;
  transform: translateY(30px);
  transition: opacity 0.6s cubic-bezier(0.25, 0.46, 0.45, 0.94) 0.2s,
              transform 0.6s cubic-bezier(0.25, 0.46, 0.45, 0.94) 0.2s;
}

.info-answer {
  font-size: 1.5rem;
  max-width: 800px;
  line-height: 1.7;
  font-weight: 500;
  opacity: 0;
  transform: translateY(30px);
  transition: opacity 0.6s cubic-bezier(0.25, 0.46, 0.45, 0.94) 0.4s,
              transform 0.6s cubic-bezier(0.25, 0.46, 0.45, 0.94) 0.4s;
}

.info-section.animate-in .info-question,
.info-section.animate-in .info-answer {
  opacity: 1;
  transform: translateY(0);
}

.info-section#contact .info-question,
.info-section#contact .info-answer {
  opacity: 1;
  transform: translateY(0);
  transition: none;
}

.info-section:nth-child(even) {
  transform: translateY(40px);
}

.info-section:nth-child(odd) {
  transform: translateY(60px);
}

.info-section.animate-in:nth-child(even),
.info-section.animate-in:nth-child(odd) {
  transform: translateY(0);
}

.info-section:nth-child(2) .info-question,
.info-section:nth-child(2) .info-answer {
  transition-delay: 0.1s, 0.3s;
}

.info-section:nth-child(3) .info-question,
.info-section:nth-child(3) .info-answer {
  transition-delay: 0.15s, 0.35s;
}

.info-section:nth-child(4) .info-question,
.info-section:nth-child(4) .info-answer {
  transition-delay: 0.2s, 0.4s;
}

.info-section:nth-child(5) .info-question,
.info-section:nth-child(5) .info-answer {
  transition-delay: 0.25s, 0.45s;
}

.info-section#contact {
  transform: translateY(0) !important;
}

.contact-content {
  display: flex;
  justify-content: center;
  width: 100%;
}

.contact-form {
  width: 100%;
  max-width: 700px;
  background-color: #111;
  border-radius: 20px;
  border: 3px solid var(--accent-color);
  padding: 2rem;
  margin: 2rem 0;
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

.form-group {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.form-label {
  color: var(--accent-color);
  font-family: var(--main-font);
  font-size: 1.2rem;
  font-weight: 600;
  margin-bottom: 0.5rem;
}

.form-input,
.form-textarea {
  background-color: #222;
  border: 2px solid #444;
  border-radius: 8px;
  color: var(--text-color);
  font-family: var(--main-font);
  font-size: 1rem;
  padding: 1rem;
  width: 100%;
  transition: border-color 0.3s, box-shadow 0.3s;
}

.form-input:focus,
.form-textarea:focus {
  outline: none;
  border-color: var(--accent-color);
  box-shadow: 0 0 0 2px rgba(255, 235, 59, 0.2);
}

.form-input::placeholder,
.form-textarea::placeholder {
  color: #888;
  font-family: var(--main-font);
}

.form-textarea {
  resize: vertical;
  min-height: 120px;
}

.submit-button {
  background-color: var(--accent-color);
  color: var(--bg-color);
  font-family: var(--main-font);
  font-size: 1.3rem;
  font-weight: 600;
  border: 3px solid var(--bg-color);
  border-radius: 50px;
  padding: 1rem 2rem;
  cursor: pointer;
  transition: transform 0.2s, box-shadow 0.2s;
  align-self: center;
  min-width: 200px;
}

.submit-button:hover {
  transform: translateY(-2px);
}

.submit-button:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

.submit-message {
  text-align: center;
  margin-top: 1rem;
  font-family: var(--main-font);
  font-size: 1.1rem;
}

.submit-message.success {
  color: #4CAF50;
}

.submit-message.error {
  color: #f44336;
}

.footer-section {
  width: 100%;
  background: var(--bg-color);
  color: var(--text-color);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 2rem 0;
  min-height: 40vh;
  text-align: center;
}

.footer-main {
  margin-bottom: 2rem;
  transition: transform 0.2s;
}

.footer-main:hover {
  transform: translateY(-2px);
}

.footer-col {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.footer-logo {
  height: 250px;
  width: auto;
}

.footer-link {
  color: var(--accent-color);
  text-decoration: none;
  font-size: 3rem;
  font-family: 'Lucida Console', Monaco, monospace;
}

.footer-copy {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.5rem;
}

.footer-copy div {
  font-size: 1.2rem;
  font-family: 'Lucida Console', Monaco, monospace;
}

@media (max-width: 768px) {
  .footer-link {
    font-size: 2rem;
  }
  
  .footer-copy div {
    font-size: 0.9rem;
  }
}

@media (max-width: 480px) {
  .footer-link {
    font-size: 1.5rem;
  }
  
  .footer-copy div {
    font-size: 0.7rem;
  }
}

@media (max-width: 700px) {
  header {
    height: auto;
    flex-direction: column;
    align-items: flex-start;
    padding: 0.5rem 0;
  }

  .header-logo {
    font-size: 1.1rem;
    margin-left: 1rem;
    margin-bottom: 0.5rem;
  }

  nav {
    width: 100%;
    justify-content: flex-start;
    gap: 1.2rem;
    padding: 0 1rem;
  }

  .nav-link {
    font-size: 1rem;
    padding: 0.3rem 0.5rem;
  }

  .info-section {
    min-height: 50vh;
    padding: 0 4vw;
    transform: translateY(30px) !important;
  }

  .info-section:nth-child(even),
  .info-section:nth-child(odd) {
    transform: translateY(30px) !important;
  }

  .info-section.animate-in:nth-child(even),
  .info-section.animate-in:nth-child(odd) {
    transform: translateY(0) !important;
  }

  .logo-container img,
  .footer-logo {
    max-width: 80vw;
    height: auto;
    max-height: 30vh;
  }

  .info-question {
    font-size: 1.5rem;
    margin-bottom: 1rem;
    text-align: left;
  }

  .info-answer {
    font-size: 1rem;
    max-width: 95vw;
    text-align: left;
  }

  .contact-button {
    font-size: 1.3rem;
    padding: 0.7rem 1.2rem;
    min-width: 160px;
    border-width: 3px;
    border-radius: 50px;
    margin: 2rem auto 0 auto;
  }

  .contact-form {
    max-width: 95vw;
    padding: 2rem 1.5rem;
    border-radius: 15px;
    margin: 1rem auto;
  }

  .form-label {
    font-size: 1rem;
  }

  .form-input,
  .form-textarea {
    font-size: 0.9rem;
    padding: 0.8rem;
  }

  .submit-button {
    font-size: 1.1rem;
    padding: 0.8rem 1.5rem;
    min-width: 180px;
  }

  .footer-main {
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 2rem 0 0 0;
    gap: 2rem;
  }

  .footer-copy {
    font-size: 0.9rem;
    padding: 1rem 0 2rem 0;
  }
}

